$white: #fff !default;
$gray-100: #f8f9fa !default;
$gray-200: #e9ecef !default;
$gray-300: #e6e6e6 !default;
$gray-400: #ced4da !default;
$gray-500: #adb5bd !default;
$gray-600: #9B9893 !default;
$gray-700: #707070 !default;
$gray-800: #343a40 !default;
$gray-900: #212529 !default;
$black: #000000 !default;

$primary: $black !default;
$secondary: #EEC32A !default;
$success: #83C51D !default;
$danger: #F57375 !default;
$light: $gray-200 !default;
$grid-breakpoint-map: (
	sm: 576px,
	md: 768px,
	lg: 992px,
	xl: 1200px,
	xxl: 1440px,
);

@mixin media-sm {
	@media screen and (min-width: map-get($grid-breakpoint-map, "sm")) {
		@content;
	}
}

@mixin media-md {
	@media screen and (min-width: map-get($grid-breakpoint-map, "md")) {
		@content;
	}
}

@mixin media-lg {
	@media screen and (min-width: map-get($grid-breakpoint-map, "lg")) {
		@content;
	}
}

@mixin media-xl {
	@media screen and (min-width: map-get($grid-breakpoint-map, "xl")) {
		@content;
	}
}

@mixin media-xxl {
	@media screen and (min-width: map-get($grid-breakpoint-map, "xxl")) {
		@content;
	}
}
$border-radius: 0.5rem !default;
$navbar-height: 100px !default;
$navbar-height-mobile: 80px !default;
$border-width: 1px !default;
$border-color: $black !default;
$container-width: 1200px !default;

@mixin custom-border {
    border: $border-width solid $border-color;
}

@mixin container-setting {
    max-width: $container-width;
    margin-left: 1rem;
    margin-right: 1rem;
    @include media-sm {
        margin-left: 1.5rem;
        margin-right: 1.5rem;
    }
    @media screen and (min-width: calc(1200px + 3rem)) {
		margin-left: auto;
        margin-right: auto;
	}
}

// 背景圖片
@mixin backgroundImage {
	background-position: center center;
	background-size: cover;
	background-repeat: no-repeat;
}

	.subtitle {
		position: relative;
		display: inline-block;
		&::before {
			content: '';
			position: absolute;
			background: currentColor;
			width: 50px;
			height: 1px;
			right: -70px;
			top: 50%;
			transform: translateY(-50%);
		}
	}
	.profile-link {
		position: relative;
		&::before {
			content: '';
			position: absolute;
			background: currentColor;
			width: 100%;
			height: 1px;
			left: 0;
			bottom: 0;
			transition: width 0.3s ease-out;
		}
		&:hover {
			opacity: 0.75;
			&::before {
				width: calc(100% + 1rem);
			}
		}
	}
	.guide-card {
		@include media-md {
			margin-top: -50px;
			z-index: 500;
		}
		.title {
			letter-spacing: 1rem;
		}
	}
	.experience-content {
		max-width: 1000px;
		.title {
			letter-spacing: 2rem;
		}
		.image-wrap {
			padding-top: 100%;
			background-image: url('~@/assets/image/avatar.jpg');
			@include backgroundImage;
		}
	}
	.work-content {
		.title {
			letter-spacing: 0.5rem;
		}
		.work {
			&.active {
				animation: fadeInLeft;
				animation-duration: 1.5s;
			}
		}
		.work-item {
			overflow: hidden;
			transition: 0.8s;
			&:hover {
				opacity: 0.6;
				.image {
					width: 87.5%;
				}
			}
			.image-wrap {
				padding-top: 75%;
				&::before, &::after {
					z-index: -1;
					position: absolute;
					content: '';
					bottom: 15px;
					left: 10px;
					width: 50%;
					top: 80%;
					max-width: 300px;
					background: rgba($black, 0.45);
					box-shadow: 0 15px 10px rgba($black, 0.45);
					transform: rotate(-3deg);
				}
				&::after {
					transform: rotate(3deg);
					right: 10px;
					left: auto;
				}
			}
			.image {
				width: 75%;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				filter: drop-shadow(0 0.25rem 0.15rem rgba($black, 0.2));
				transition: 0.8s;
			}
		}
	}
	.contact-content {
		max-width: 750px;
		.title {
			letter-spacing: 0.5rem;
			top: -1.5rem;
			left: 50%;
			transform: translateX(-50%);
		}
		.email {
			&:hover {
				opacity: 0.6;
			}
		}
	}
